<template>
  <footer id="footer">
    <div class="info row">
      <div class="contact col-sm-6">
        <span>KaiZ</span>
        <div id="map"></div>
      </div>
      <div class="about col-sm-4 py-sm-0 py-3">
        <span>KaiZ</span>
        <div>
          <p>CSS3 CSS  ... request </p>
        </div>
      </div>
      <div class="follow col p-sm-0">
        <span>KaiZ</span>
        <router-link to="/" tag="a">
          <i class="fab fa-twitter-square"></i>Twitter
        </router-link>
        <router-link to="/" tag="a">
          <i class="fab fa-facebook-square"></i>Facebook
        </router-link>
        <router-link to="/" tag="a">
          <i class="fab fa-instagram"></i>Instagram
        </router-link>
        <router-link to="/" tag="a">
          <i class="fab fa-line"></i>Line
        </router-link>
      </div>
    </div>
    <div class="copyright text-center">
      <font style="vertical-align: inherit;">Kaiz.BB</font>
    </div>
  </footer>
</template>

<script>
export default {
  // name: "Restaurants",
  data() {
    return {
      map: null,
      // 
      lat: 25.063891,
      lng: 121.642334
    };
  },
  methods: {
    // 
    initMap() {
      const vm = this;
      //  Map  map  id  map 
      vm.map = new google.maps.Map(document.getElementById("map"), {
        styles: [
          {
            elementType: "geometry",
            stylers: [
              {
                hue: "#ff4400"
              },
              {
                saturation: -68
              },
              {
                lightness: -4
              },
              {
                gamma: 0.72
              }
            ]
          },
          {
            featureType: "road",
            elementType: "labels.icon"
          },
          {
            featureType: "landscape.man_made",
            elementType: "geometry",
            stylers: [
              {
                hue: "#0077ff"
              },
              {
                gamma: 3.1
              }
            ]
          },
          {
            featureType: "water",
            stylers: [
              {
                hue: "#00ccff"
              },
              {
                gamma: 0.44
              },
              {
                saturation: -33
              }
            ]
          },
          {
            featureType: "poi.park",
            stylers: [
              {
                hue: "#44ff00"
              },
              {
                saturation: -23
              }
            ]
          },
          {
            featureType: "water",
            elementType: "labels.text.fill",
            stylers: [
              {
                hue: "#007fff"
              },
              {
                gamma: 0.77
              },
              {
                saturation: 65
              },
              {
                lightness: 99
              }
            ]
          },
          {
            featureType: "water",
            elementType: "labels.text.stroke",
            stylers: [
              {
                gamma: 0.11
              },
              {
                weight: 5.6
              },
              {
                saturation: 99
              },
              {
                hue: "#0091ff"
              },
              {
                lightness: -86
              }
            ]
          },
          {
            featureType: "transit.line",
            elementType: "geometry",
            stylers: [
              {
                lightness: -48
              },
              {
                hue: "#ff5e00"
              },
              {
                gamma: 1.2
              },
              {
                saturation: -23
              }
            ]
          },
          {
            featureType: "transit",
            elementType: "labels.text.stroke",
            stylers: [
              {
                saturation: -64
              },
              {
                hue: "#ff9100"
              },
              {
                lightness: 16
              },
              {
                gamma: 0.47
              },
              {
                weight: 2.7
              }
            ]
          }
        ],
        // 
        center: { lat: vm.lat, lng: vm.lng },
        //  0-20
        zoom: 17,
        // 
        maxZoom: 20,
        // 
        minZoom: 3,
        // 
        streetViewControl: false,
        // 
        mapTypeControl: false
      });
    },
    // 
    setMarker() {
      const vm = this;
      // 
      const marker = new google.maps.Marker({
        // 
        position: { lat: this.lat, lng: this.lng },
        // 
        map: vm.map
      });
    }
  },
  mounted() {
    const vm = this;
    vm.initMap();
    vm.setMarker();
  }
};
</script>


<style lang="scss" scoped>
@import "@/assets/helpers/breakpoint.scss";
#footer {
  background: $color-green;
  position: relative;
  z-index:0;
  bottom: 0;
  padding: 1rem 5rem 0 5rem;
  @include mobile() {
    padding: 1rem 1rem 0 1rem;
  }
  @include pad() {
    padding: 1rem 1.5rem 0 1.5rem;
  }
  & > .info {
    margin: 0 auto;
    span {
      font-size: 1.2rem;
      color: white;
    }
  }
  .contact {
    #map {
      margin: 10px 0;
      width: 100%;
      max-width: 900px;
      height: 150px;
      background: #000;
    }
  }
  .about {
    p {
      color: #d4d4d4;
      padding: 5px 0;
      line-height:1.3rem;
      text-align:justify;
    }
  }
  .follow {
    a {
      display: block;
      color: $color-gray;
      text-decoration: none;
      padding: 5px 0;
      &:hover {
        opacity: 0.7;
      }
      i {
        padding-right: 0.3rem;
      }
    }
  }
  .copyright {
    padding: 1rem 0;
    color: $color-gray;
    font-size: 0.8rem;
    border-top: 1px solid $color-gray;
  }
}
</style>




