<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="row mt-4">
            <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
                <div class="card border-0 shadow-sm">
                    <div style="height: 150px; background-size: cover; background-position: center"
                    :style="{backgroundImage:`url(${item.imageUrl})`}">
                    </div>
                    <div class="card-body">
                    <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
                    <h5 class="card-title">
                        <a href="#" class="text-dark">{{ item.title }}</a>
                    </h5>
                    <p class="card-text">{{ item.content }}</p>
                    <div class="d-flex justify-content-between align-items-baseline">
                        <!-- <div class="h5">2,800 </div> -->
                        <del class="h6" v-if="item.origin_price > 0">{{item.origin_price}} </del>
                        <div class="h5">{{item.price}}</div>
                    </div>
                    </div>
                    <div class="card-footer d-flex">
                    <button type="button" class="btn btn-outline-secondary btn-sm"
                        @click="getProduct(item.id)"><!--vfor=>itemid-->
                        <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                        
                    </button>
                    <button type="button" class="btn btn-outline-danger btn-sm ml-auto"
                        @click="addtoCart(item.id)">
                        <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                        
                    </button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body"> <!---->
            <img :src="product.imageUrl" class="img-fluid" alt="">
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">{{ product.origin_price }} </div>
              <del class="h6" v-if="product.price"> {{ product.origin_price }} </del>
              <div class="h4" v-if="product.price"> {{ product.price }} </div>
            </div>
            <select name="" class="form-control mt-3" v-model="product.num">
              <option :value="num" v-for="num in 10" :key="num">
                 {{num}} {{product.unit}}
              </option>
            </select>
          </div>
          <div class="modal-footer"><!---->
            <div class="text-muted text-nowrap mr-3">
               <strong>{{ product.num * product.price }}</strong> 
            </div>
            <button type="button" class="btn btn-primary"
              @click="addtoCart(product.id,product.num)">
              <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
              
            </button>
          </div>
          </div>
          </div>
        </div>
        <div class="my-5-row justify-content-center"><!---->
            <div class="col-md-6" style="margin:0 auto;">
                <div class="h2 text-center card-title"></div>
                <table class="table">
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in cart.carts" :key="item.id">
                            <td>
                                <button class="btn btn-danger btn-sm"
                                @click="deleteCart(item.id)">
                                    <i class="fas fa-trash" title=""></i>
                                </button>
                            </td>
                            <td>
                                {{item.product.title}}
                                <div class="text-success" v-if="item.coupon" style="font-size:.4rem;">
                                    
                                </div>
                            </td>
                            <td>{{item.qty}}{{item.product.unit}}</td>
                            <td>{{Math.round(item.final_total)}}</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                            <td colspan="3" class="text-left"></td>
                            <td class="text-right">{{cart.total}}</td>
                        </tr>
                        <tr v-if="cart.total !== cart.final_total">
                            <td colspan="3" class="text-left text-success"></td>
                            <td class="text-right">{{Math.round(cart.final_total)}}</td>
                        </tr>
                    </tfoot>
                </table>
                <div class="input-group mb-3 input-group-sm">
                    <input type="text" class="form-control" placeholder="" v-model="coupon_code">
                    <div class="input-group-append">
                        <button type="button" class="btn btn-outline-secondary" 
                        @click="addCoupon">
                            
                        </button>
                    </div>
                </div>
                <div class="text-center">
                    <span v-if="couponMessage == '!' ||
                    couponMessage == ''" class="text-danger">{{couponMessage}}</span>
                    <span v-else class="text-success">{{couponMessage}}</span>
                </div>
                <div class="my-5 row justify-content-center">
                <form class="col-md-6" @submit.prevent="createOrder">
                    <div class="form-group">
                    <label for="useremail">Email</label>
                    <input type="email" class="form-control" name="email" id="useremail"
                        v-validate="'required|email'"
                        :class="{'is-invalid':errors.has('email')}"
                        v-model="form.user.email" placeholder=" Email">
                    <span class="text-danger" v-if="errors.has('email')">
                        {{ errors.first('email') }}
                    </span>
                    </div>
                
                    <div class="form-group">
                    <label for="username"></label>
                    <input type="text" class="form-control" name="name" id="username"
                        :class="{'is-invalid':errors.has('name')}"
                        v-model="form.user.name" v-validate="'required'" placeholder="">
                    <span class="text-danger" v-if="errors.has('name')"></span>
                    </div>
                
                    <div class="form-group">
                    <label for="usertel"></label>
                    <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="">
                    </div>
                
                    <div class="form-group">
                    <label for="useraddress"></label>
                    <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
                        :class="{'is-invalid':errors.has('address')}"
                        placeholder="" v-validate="'required'">
                    <span class="text-danger" v-if="errors.has('address')">
                          
                    </span>
                    </div>
                
                    <div class="form-group">
                    <label for="comment"></label>
                    <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                    </div>
                    <div class="text-right">
                    <button class="btn btn-danger"></button>
                    </div>
                </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';

export default {
    data(){
        return{
            products:[],
            product:{},//
            status:{//
                loadingItem:'',//id
            },
            form:{ //
                user:{
                    name:'',
                    email:'',
                    tel:'',
                    address:'',
                },
                message:'',
            },
            isLoading:false,
            cart:{},//
            coupon_code:'',//
            couponMessage:'',//
        };
    },
    methods:{
        getProducts(){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=:page`;
            console.log(url)
            vm.isLoading = true;
            this.$http.get(url).then((response) => {
                vm.products = response.data.products;
                console.log(response);
                vm.isLoading = false;
            })
        },
        getProduct(id){ //
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
            vm.status.loadingItem = id;
            this.$http.get(url).then((response) => {
                vm.product = response.data.product;
                $('#productModal').modal('show');
                console.log('',response);
                vm.status.loadingItem = '';
            })
        },
        addtoCart(id,qty = 1){//1
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            vm.status.loadingItem = id;
            const cart = {  //
                product_id:id,
                qty
            }
            this.$http.post(url,{data : cart}).then((response) => {
                console.log('',response);
                vm.status.loadingItem = '';
                vm.getCart();//
                $('#productModal').modal('hide');
            });
        },
        getCart(){//
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            console.log(url);
            vm.isLoading = true;
            this.$http.get(url).then((response) => {
                vm.cart = response.data.data;
                console.log(response);
                vm.isLoading = false;
            })
        },
        deleteCart(id){
            console.log(id);
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
            vm.isLoading = true;
            this.$http.delete(url).then((response) => {
                console.log('',response.data.message);
                vm.getCart();//
                vm.isLoading = false;
            })
        },
        addCoupon(){ //
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
            const coupon = {
                code:vm.coupon_code,//
            }
            vm.isLoading = true;
            this.$http.post(url,{data: coupon }).then((response) => {
                console.log('',response.data);
                vm.couponMessage = response.data.message;
                vm.getCart();//
                vm.isLoading = false;
            });
        },
        createOrder(){ //
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
            const order = vm.form;
            // vm.isLoading = true;
            this.$validator.validate().then((result) => {
                if (result) { //email
                    this.$http.post(url,{data: order }).then((response) => {
                        console.log('',response);
                        if(response.data.success){// 
                            vm.$router.push(`/customer_checkout/${response.data.orderId}`)
                        }
                        vm.isLoading = false;
                    });
                }else{
                    console.log('')
                }
            });    
        },
    },
    created(){
        this.getProducts();
        this.getCart();//
    },
};
</script>



