<template>
  <div id="news" class="pt150">
    <div class="row m-0 p-0">
      <div class="box col-12 col-md-6">
        <img src="@/images/news/01.jpg" alt>
        <div class="info">
          <h2 class="py-2 h4"></h2>
          <p class="mb-2">301,</p>
        </div>
        <div class="more">See More</div>
      </div>
      <div class="box col-12 col-md-6">
        <img src="@/images/news/02.jpg" alt>
        <div class="info">
          <h2 class="py-2 h4"></h2>
          <p class="mb-2"></p>
        </div>
        <div class="more">See More</div>
      </div>
    </div>
    <div class="row m-0 p-0">
      <div class="box col-12 col-md-6">
        <img src="@/images/news/03.jpg" alt>
        <div class="info">
          <h2 class="py-2 h4"></h2>
          <p class="mb-2">3</p>
        </div>
        <div class="more">See More</div>
      </div>
      <div class="box col-12 col-md-6">
        <img src="@/images/news/04.jpg" alt>
        <div class="info">
          <h2 class="py-2 h4">5</h2>
          <p class="mb-2"></p>
        </div>
        <div class="more">See More</div>
      </div>
    </div>
    <div class="row m-0 p-0">
      <div class="box col-12 col-md-6">
        <img src="@/images/news/05.jpg" alt>
        <div class="info">
          <h2 class="py-2 h4"></h2>
          <p class="mb-2"></p>
        </div>
        <div class="more">See More</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  created() {
    document.querySelector("html").classList.contains("box");
  }
};
</script>


<style lang="scss" scoped>
@import "@/assets/helpers/breakpoint.scss";

#news {
  max-width: 1200px;
  margin: 0 auto 1rem auto;
  .box {
    overflow: hidden;
    position: relative;
    cursor: pointer;
    text-align: center;
    align-items: center;
    @include pad(){
      margin:15px 0;
    }
    img {
      width: 100%;
    }
    &>.info{
      @include pad(){
        position:absolute;
        bottom:0;
        color:white;
        background:$color-bgActive;
        margin-right:15px;
      }
    }
    h2{
      font-weight:bold;
    }
    p {
      line-height: 1.5rem;
      overflow: hidden;
      width: 100%;
      display: -webkit-box;
      -webkit-line-clamp:1;
      -webkit-box-orient: vertical; 
      @include pad(){
        // max-width: 345px;
        padding:0 15px;
      }
    }
  }
  .more {
    transition: 0.6s;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -100%);
    color: #fff;
    font-family: impact;
    font-size: 2rem;
    letter-spacing: 0.5rem;
    width: calc(80% - 30px);
    border: 5px solid #fff;
    padding: 25px 0;
  }
  .box:hover {
    img {
      filter: grayscale(0.3);
    }
    .more {
      opacity: 1;
      @include pad(){
        display:none;
      }
    }
  }
}
</style>



